export default {
  formGroupStyle() {
    return {
      width: '100%',
      marginBottom: '5px',
    };
  },
  inputStyle() {
    return {
      width: '100%',
      display: 'block',
    };
  },
  smallTextStyle() {
    return {
      'maxWidth': '200px',
    };
  },
};



